.react-chart {
  -webkit-transition: 0.7s;
  -o-transition: 0.7s;
  transition: 0.7s;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top:0;
  left: 0;
  background-color: rgba(34,34,34, 0.8);
  height: 100%;
}
.react-chart.show {
  opacity: 1;
  visibility: visible;
}



