.issue-block {
    margin-bottom: 5px;
    height: 220px;
    width: calc(50% - 10px);
    float: left;
    display: block;
    position: relative;
    background: #fff;
    color:#000;
    padding: 10px;
    overflow: hidden;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-left: 5px;
    margin-right: 5px;
}
.issue-block.Blocker {
    border-top: 3px solid red;
}
.header .type{
    float:left;
}
.header .task-id {
    margin-left: 5px;
    float: left;
}
.header .assignee {
    display: block;
    position: relative;
}
.header {
    position: relative;
    display: block;
    height: 70px;
}

.avatar img {
    width: 22px;
    height: 22px;
    float: right;
    margin-left: 5px;
}
.name {
    text-align: right;
    font-size: 12px;
    margin-top: 5px;
}

.summary {
    font-size: 15px;
    margin-bottom: 15px;
    height: 75px;
}
.priority {
    color:red;
    display: block;
    float: none;
    clear: left;
    margin-top: 0px;
}
.priority img {
    width: 16px;
    height: 16px;
    float:left;

}



